.header {
  margin-top: 6rem;
  height: calc(100vh - 5rem);
  display: grid;
  place-items: center;
  background: var(--color-white);
  transition: var(--transition);
}

.header-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.header-profile {
  width: 18rem;
  height: 22rem;
  border-radius: 9rem 9rem 1.5rem 1.5rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
}

.header-container p {
  width: 35rem;
  text-align: center;
  margin: 0.6rem 0 2rem;
}

.header-cta {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

.header-socials {
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.header-socials a {
  width: 1.8rem;
  aspect-ratio: 1/1;
  background: var(--color-light);
  color: var(--color-black);
  display: grid;
  place-items: center;
  border-radius: var(--border-radius-3);
}

.header-socials a:hover {
  background: var(--color-primary);
  color: var(--color-white);
  transform: translateX(-0.5rem);
}

@media screen and (max-width: 1024px) {
  header {
    margin-top: 0;
    height: 100vh;
  }
}

@media screen and (max-width: 600px) {
  .header-container {
    background: red;
  }

  .header-profile {
    width: 14rem;
    height: 17rem;
    max-width: 60%;
    max-height: 35vh;
  }

  .header-cta {
    gap: 1rem;
  }

  .header-socials {
    display: none;
  }

  .header-container p {
    width: 100%;
  }
}
